<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- layout布局 -->
      <el-row :gutter="20">
        <!-- 搜索框 -->
        <el-col :span="9">
          <div>
            <el-input placeholder="请输入内容" v-model="queryParams.query" @input="initUserList" clearable>
              <el-button slot="append" icon="el-icon-search" @click="initUserList"></el-button>
            </el-input>
          </div>
        </el-col>
        <el-col :span="4" :offset="11">
          <div>
            <el-button type="primary" icon="el-icon-user" @click="addUserFormVisible = true">添加用户</el-button>
          </div>
        </el-col>
      </el-row>
      <!-- 用户表格区域 -->
      <el-table :data="userList" style="width: 100%" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="mobile" label="电话"></el-table-column>
        <el-table-column prop="role_name" label="角色"></el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="changeStatus(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" @click="showEditUserVisible(scope.row.id)" size="mini"></el-button>
            <el-button type="danger " icon="el-icon-delete" @click="deleteUser(scope.row.id)" size="mini"></el-button>
            <!-- 文字提示 enterable:鼠标无法进入提示信息框里面 -->
            <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="allotRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryParams.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

    <!-- dialog--添加用户 -->
    <el-dialog title="添加用户" :visible.sync="addUserFormVisible" @close="addUserClose">
      <el-form :model="addUserForm" :rules="addUserRules" ref="addUserRef">
        <el-form-item label="用户名" prop="username" :label-width="formLabelWidth">
          <el-input v-model="addUserForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" :label-width="formLabelWidth">
          <el-input v-model="addUserForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址" prop="email" :label-width="formLabelWidth">
          <el-input v-model="addUserForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile" :label-width="formLabelWidth">
          <el-input v-model="addUserForm.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部取消/确定按钮 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="addUserFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitAddUserForm">确 定</el-button>
      </div>
    </el-dialog>

    <!-- dialog--编辑用户 -->
    <el-dialog title="编辑用户" :visible.sync="editUserFormVisible" @close="editUserClose">
      <el-form :model="editUserForm" :rules="editUserRules" ref="editUserRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="editUserForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址" prop="email" :label-width="formLabelWidth">
          <el-input v-model="editUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile" :label-width="formLabelWidth">
          <el-input v-model="editUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editUserFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitEditUserForm(editUserForm.id,editUserForm.email,editUserForm.mobile)">确 定</el-button>
      </span>
    </el-dialog>

    <!-- dialog--分配角色 -->
    <el-dialog title="分配角色" :visible.sync="allotRoleVisible" @close="setRoleClose">
      <el-form :model="allotRoleForm" :label-width="formLabelWidth">
        <el-form-item label="用户名">
          <el-input v-model="allotRoleForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="用户角色">
          <el-input v-model="allotRoleForm.role_name" disabled></el-input>
        </el-form-item>
        <el-form-item label="修改角色">
          <el-select v-model="selectRole" placeholder="请选择">
            <el-option v-for="item in roleList" :key="item.id" :label="item.roleName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="allotRoleVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRoleSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.initUserList()
  },
  data() {
    // 创建自定义邮箱验证规则
    let checkMail = function (rule, value, cb) {
      let regMail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regMail.test(value)) {
        cb()
      } else {
        cb(new Error('请输入合法的邮箱'))
      }
    }
    // 创建自定义手机号验证规则
    let checkMobile = function (rule, value, cb) {
      let regMobile =
        /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        cb()
      } else {
        cb(new Error('请输入合法的手机号'))
      }
    }
    return {
      // 分页数据
      queryParams: {
        // 请求参数
        query: '', // 搜索数据
        pagenum: 1, // 显示第几页
        pagesize: window.sessionStorage.getItem('pageSize')
          ? parseInt(window.sessionStorage.getItem('pageSize'))
          : 2, // 每页显示几条数据
      },
      userList: [], // 用户数据数组信息
      total: 2, // 分页--总数量

      selectRole: '',
      roleList: [], // 角色列表

      addUserFormVisible: false, // dialog(添加用户)是否可见
      editUserFormVisible: false, // dialog(编辑用户)是否可见
      allotRoleVisible: false, // dialog(分配角色)是否可见
      formLabelWidth: '90px', // dialog的label长度
      // dialog添加的用户信息
      addUserForm: {
        username: '',
        password: '',
        email: '',
        mobile: '',
      },
      // dialog编辑的用户信息
      editUserForm: {
        id: '',
        username: '',
        role_id: '',
        email: '',
        mobile: '',
      },
      // dialog分配角色的信息
      allotRoleForm: {},

      // dialog添加用户表单验证规则
      addUserRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 4, max: 15, message: '长度在 4 到 5 个字符', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '长度在 6 到 15 个字符',
            trigger: 'blur',
          },
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { validator: checkMail, trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' },
        ],
      },
      // dialog编辑用户表单验证规则
      editUserRules: {
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { validator: checkMail, trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' },
        ],
      },
    }
  },
  methods: {
    // 初始化获取数据
    async initUserList() {
      let { data: res } = await this.$http.get('users', {
        params: this.queryParams,
      })
      console.log('用户数据列表：', res)
      if (res.meta.status !== 200)
        return this.$message.error('获取用户信息失败！')
      this.userList = res.data.users
      this.total = res.data.total
    },

    // 更新用户(单选框)状态
    async changeStatus(scope) {
      let { data: res } = await this.$http.put(
        `users/${scope.id}/state/${scope.mg_state}`
      )
      if (res.meta.status !== 200) {
        scope.status = !scope.status // 更新状态失败之后,将单选框的状态回退到失败之前
        return this.$message.error('更新用户状态失败!')
      }
      this.$message.success('更新用户状态成功!')
    },

    // 分页--每页显示的数据行数调整
    handleSizeChange(newSize) {
      console.log('每页显示的数据条数:', newSize)
      window.sessionStorage.setItem('pageSize', newSize)
      this.queryParams.pagesize = newSize
      this.initUserList()
    },
    // 分页--页数
    handleCurrentChange(newPageLine) {
      console.log('当前页数:', newPageLine)
      this.queryParams.pagenum = newPageLine
      this.initUserList()
    },

    // 关闭dialog清空表单
    addUserClose() {
      this.$refs.addUserRef.resetFields()
    },
    // 提交添加用户的表单信息
    submitAddUserForm() {
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) return false
        let { data: res } = await this.$http.post('users', this.addUserForm)
        console.log(res)
        if (res.meta.status !== 201) return this.$message.error('添加用户失败!')
        this.$message.success('添加用户成功')
        this.initUserList()
        this.addUserFormVisible = false
      })
    },

    // 关闭编辑用户表单清空表单信息
    editUserClose() {
      this.$refs.editUserRef.resetFields()
    },
    // 编辑用户的按钮事件
    async showEditUserVisible(id) {
      this.editUserFormVisible = true
      let { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200)
        return this.$message.error('获取用户信息失败!')
      this.editUserForm = res.data
    },
    // 提交编辑用户的表单信息
    submitEditUserForm(id, email, mobile) {
      let obj = {
        id,
        email,
        mobile,
      }
      this.$refs.editUserRef.validate(async (valid) => {
        if (!valid) return false
        let { data: res } = await this.$http.put('users/' + id, obj)
        console.log(res)
        this.editUserFormVisible = false
        if (res.meta.status !== 200)
          return this.$message.error('修改用户信息失败!')
        this.$message.success('修改用户信息成功!')
        this.initUserList()
      })
    },

    // 删除用户
    deleteUser(id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(async () => {
          let { data: res } = await this.$http.delete('users/' + id)
          if (res.meta.status !== 200)
            return this.$message.error('删除用户失败!')
          this.$message.success('删除用户成功!')
          // 判断当前页的数据是否已经被删除完毕
          // 如果为1表示删除完毕，接着判断当前页数是否大于1
          console.log(this.userList) // 有一条数据
          // 大于1则页数-1，等于1则页数为1
          if (this.userList.length === 1) {
            this.queryParams.pagenum =
              this.queryParams.pagenum === 1 ? 1 : this.queryParams.pagenum - 1
          }
          this.initUserList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },

    // 分配角色按钮
    async allotRole(userInfo) {
      // 将当前选择的用户行的信息添加到allotRoleForm属性中
      this.allotRoleForm = userInfo
      let { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200)
        return this.$message.error('获取角色列表失败！')
      this.roleList = res.data // 添加角色列表到roleList属性
      console.log(res.data)
      this.allotRoleVisible = true
    },
    // 确认分配角色
    async allotRoleSure() {
      if (!this.selectRole) {
        return this.$message.error('请选择角色权限')
      }
      let { data: res } = await this.$http.put(
        `users/${this.allotRoleForm.id}/role`,
        {
          rid: this.selectRole,
        }
      )
      console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('修改用户角色失败！')
      this.$message.success('修改用户角色成功！')
      this.initUserList()
      this.allotRoleVisible = false
    },
    // 分配角色的dialog关闭
    setRoleClose() {
      this.selectRole = ''
      this.allotRoleForm = {}
    },
  },
}
</script>

<style lang="less" scoped>
// 卡片视图区域css
.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}
</style>